@font-face {
  font-family: Regular;
  src: url(./../assets/Semibold.otf);
  font-weight: bold;
}

@font-face {
  font-family: Regular;
  src: url(./../assets/Regular.otf);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Regular;
  src: url(./../assets/RegularItalic.otf);
  font-weight: normal;
  font-style: italic;
}

.btn {
  font-size: 0.875rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  border-radius: 0.25rem;
  border-style: none;
  border-width: 0;
  outline: none;
  border: 0px;
  cursor: pointer;
  text-transform: none;
  overflow: visible;
  /* line-height: 1.15; */
  margin: 0;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;

  text-align: center;
  display: table-cell;
}

.btn-primary {
  background-color: #006efe;
  color: #fff;
}

.btn-primary:hover {
  background-color: #005cd3;
}

.btn-cancel {
  background-color: #d71551;
  color: rgba(0, 0, 0, 0.8);
}

.btn-cancel:hover {
  background-color: #f2f2f2;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  overflow: hidden;
}

.ace-dracula {
  background: #24262c;
  color: #eceef5;
  font-family: monospace, "Courier New", Courier, monospace;
  .ace_marker-layer .ace_active-line {
    background: #2c313c;
  }

  .ace_marker-layer .ace_selection {
    background: #3e4451;
  }
  .ace_string {
    color: #ec7cbb;
    font-weight: bolder;
  }
  .ace_support.ace_constant {
    color: #3e4451;
  }
}
