@import "./Base.scss";
@import "./Markdown.scss";

#root {
  height: 100vh;
  width: 100vw;
}

/* Main layout container */
#ultra {
  height: 100%;
  width: 100%;
}

#nav-bar {
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0px 10px 0px 10px;
  background-color: #f8f8f8;
  border-bottom: 1px solid #e7e7e7;
  box-shadow: 0 0 13px 0 rgba(47, 47, 47, 0.1);
  font-weight: bold;
  font-size: 18px;
  .nav-right {
    margin-left: auto;
    display: flex;
    .print-btn {
      margin-left: 5px;
    }
  }
  .v-animal-avatar {
    margin-right: 5px;
  }
}

/* Side by side editor and preview */
#content {
  height: calc(100% - 50px);
  width: 100%;
  display: flex;
  #editor {
    flex: 1;
    height: 100%;
    max-width: 50%;
  }
  .preview {
    flex: 1;
    #preview-padding {
      height: 100%;
      width: 100%;
      padding: 20px;
    }
  }
}

@media print {
  #editor,
  #nav-bar {
    display: none;
  }

  .preview {
    #preview-padding {
      padding: 0px !important;
    }
  }

  @page {
    size: letter;
    margin: 0.6in;
  }
}
